.blockNewItems {
    width: 310px;
    height: 80px;
    border: 2px solid black;
    margin: 10px auto;
    padding: 20px 0;
    box-sizing: border-box;
}
@media (min-width: 500px) {
    .blockNewItems {
        width: 100%;
    }
    .fillingLine {
        flex-grow: 1;
       
    }
    .button {
        margin-right: 25px;
    }
    .g {
        width: 100%;
        display: flex;
        align-items: center;
    }
}

.buttonNotes {
    appearance: none;
}

.fillingLabel {
    cursor: pointer;
    position: relative;
    padding-right: 35px;
    
}

.fillingLabel::before {
    position: absolute;
    content: "";
    top: 0px;
    left: 35px;
    width: 15px;
    height: 15px;
    border: 2px solid black;
}

.fillingLabel::after {
    position: absolute;
    content: "";
    top: 5px;
    left: 38px;
    width: 10px;
    height: 5px;
    transform: rotate(-45deg);
    border: 2px solid black;
    border-top: none;
    border-right: none;
    opacity: 0;
}

.buttonNotes:checked ~ .fillingLabel::after {
    opacity: 1;
}

.fillingLine {
    border: 2px solid black;
    height: 25px;
    
}
.fillingLine:focus {
    outline:none;
}
.button {
    cursor: pointer;
    font-size: 25px;
    background-color: transparent;
    border: none;
    vertical-align: middle;
    outline: none;
    position: relative;
    left: 5px;
}